<template>
    <section  class="main-content no-left-bar">      
        <div class="right-content">
            <div class="col-md-12 p-0">
                <div class="theme-primary partition">
                    <span class="p-name text-white">{{$t('ProductsGroupList')}}</span>
                    <span class="p-actions float-right">
                        <em title="AddNew"  class="p-action-btn text-white" @click="AddNewGroup(0)"><em
                                class="fa fa-plus"></em><br />{{$t('AddNew')}}</em>
                        <em  @click="DeleteRFID()" title="DeleteAssetCatalogue" id="DeleteAssetCatalogue"
                            class="table-head-btn p-action-btn text-white disabled iconenable">
                            <em
                                class="fa fa-trash"></em><br />{{ $t('Delete') }}
                        </em>
                    </span>
                    
                </div>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 pt-2 p-0">
                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                        <div class="row mt-0">
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                <div class="form-group">
                                    <input class="form-control" type="text" placeholder="Search By Group Name"
                                        v-model="txtsearch" autocomplete="off" name="search"/>
                                </div>
                            </div>
                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                <div class="search-btm-btn background-0 text-left  m-0">
                                    <em  title="Search" class="btn btn-success mr-1 px-4"
                                        @click="SearchData"><em class="fa fa-search text-white"></em></em>
                                    <em title="Clear" class="btn btn-danger clearsearchtext px-4"
                                        @click="ResetData"><em class="fa fa-refresh"></em></em>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="listing mt-2">
                <div class="table-responsive">
                    <table class="table table-bordered mt-0" id="itmgrplisting">
                        <thead class="thead-bg">
                            <tr>
                                <th style="width:50px !important;" @change="onChange" class="text-center" id="nonresize"><input id="chkAll"
                                        class="chkItems" type="checkbox"></th>
                                <th ><span href="javascript:;" @click="sortdata($event)"  class="sort"  id="GroupName">{{$t('GroupName')}}</span></th>
                                <th ><span href="javascript:;"  @click="sortdata($event)" class="sort"  id="GroupDetails">{{$t('Detail')}}</span></th>
                                <th><span href="javascript:;" class="" id="">{{$t('DeviceName')}}</span></th>                                
                                <th><span>{{$t('AssignedReader')}}</span></th>
                                <th class="text-center"><span>{{$t('TotalItems')}}</span></th>
                                <th class="text-center"><span>{{$t('Status')}}</span></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in GroupData" :key="index">
                                <td class="text-center">
                                    <input v-if="item.group_id !=null" class="chkItems" @change="onChange" :value="item.group_id" type="checkbox">
                                    <input v-else class="chkItems" disabled="disabled" type="checkbox">
                                </td>

                                <td>
                                    <em  :value="item.group_id" @click="editNewGroup(item.group_id)"><span
                                            class="text-overflow-dynamic-container" style="color:#007bff;">{{ item.group_name }}</span>
                                    </em>                                    
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.group_details }}</span>
                                </td>
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.device_name }}</span>
                                </td>
                               
                                <td>
                                    <span class="text-overflow-dynamic-container">{{ item.item_description }}</span>
                                </td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><em class="fa fa-briefcase" aria-hidden="true"></em><span
                                            class="noti-circle noti-blue">{{ item.TotalItemsCount }}</span></span>
                                </td>

                                <td>
                                    <div class="cstm-drop-btn">
                                        <select  v-model="item.STATUS_ID" v-on:change="UpdataStatus(item.STATUS_ID,item.group_id);"  :class="{ 'ddlupdatestatus w-80 btn-success':item.STATUS_ID==1001, 'ddlupdatestatus w-80 btn-danger':item.STATUS_ID==1002}">
                                            <option v-for="(items,index) in StatusData"   :key="index" v-bind:value="items.STATUS_ID">{{items.STATUS}}</option>
                                        </select>
                                    </div>		
                                </td>
                            </tr>
                            <tr v-if="noRecord">
                                <td colspan="8" class="text-center text-danger no-record">{{$t('NoRecordfound')}}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <Pager v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPage" prev-text="Prev"
                    first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import Pager from '../../components/Common/Pager.vue';
import StatusList from '../../components/Common/ListingStatus.vue'
export default {
    components: {
        Pager, StatusList
    },
    data() {
        return {
            txtsearch: '',
            statusIds: '',
            LocationId: '',
            sortBy: '',
             sortExp: '',
            IsDefault: '',
            GroupData: [],
            noRecord: false,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            isLoading: false,
            StatusData: []
        }
    },
    created: function () {
        this.IsDefault = this.GetUserInfo.IsDefault;
        this.GetGroupList();
        this.GetStatusList();
    },
    methods: {       
        GetGroupList: function () {
            var url = `search=${this.txtsearch}&statusIds=${this.statusIds}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.sortBy}&sortExp=${this.sortExp}&IsDefault=${this.IsDefault}`;
            var vueObj = this;
            DataService.GetItemGroupList(url).then(response => {
                if (response.data != null) {
                    if (response.data.data.length > 0) {
                        vueObj.GroupData = response.data.data;                        
                        vueObj.TotalRecords = response.data.data[0].TOTALRECORDS
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');                       
                    }
                    else {
                        vueObj.GroupData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;
                    }
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
            });
        },
        SearchData: function () {
            this.GetGroupList();
        },
        ResetData: function () {
            this.txtsearch = '';
            this.GetGroupList()
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetGroupList();
        },
        
        sortdata: function ($event) {      
       
           this.sortBy = $event.currentTarget.id;
           this.sortExp=(this.sortExp=='DESC')?'ASC':'DESC'
           this.GetGroupList();
        },

        AddNewGroup(Groupid) {    
            this.$router.push({ name: 'ManageGroup', params: { id: Groupid } }); 
        },
        editNewGroup(Groupid){
            this.$router.push({ name: 'ManageGroup', params: { id: Groupid } });    
        },    
        DeleteRFID: function () {
            debugger
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });

            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Item")), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "Talygen_IoT_Items_Group",
                        primarycolname: "Group_Id"
                    };
                    vm.isLoading = true;
                    var poststing = JSON.stringify(postJSON);
                    DataService.DeleteGroupList(poststing).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group_Item')]), "Success", true, vm.$t('Alert'));
                            vm.GetGroupList();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        GetStatusList:function() {            
            var vm = this;
            vm.isLoading=true;           
            var param = 'statusType=AssetManagement';     
            DataService.GetStatusList(param)
                .then(response => {                    
                    vm.StatusData = JSON.parse(response.data).filter(x => x.STATUS === 'Active' || x.STATUS === 'Inactive');
                });
        },
        UpdataStatus(statusId,gropid){           
            var vm = this;
            vm.isLoading=true;
              const Object = {
                ID: gropid,
                StatusId: statusId,
                IsFor: 'TALYGEN_IOT_ITEMS_GROUP'
            }
            var PostString = JSON.stringify(Object);
            DataService.CommonUpdateStatus(PostString).then(function (response) {              
                if (response.data != null) {
                    if (response.data.status === 'Success') {
                        vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success",vm.$t('Alert'));
                    }else{
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                }
                            
            });
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#DeleteAssetCatalogue').removeClass('disabled');
            }
            else {
                $('#DeleteAssetCatalogue').addClass('disabled');
            }

        },
    }
}

</script>